




















































































/*1.标题*/
#page-title {
    margin: 20px 0 30px;
    font-size: 20px;
    text-align: center;
}
/*2.新闻类别选择、加载、新闻标题*/
#news-classify {
    text-align: center;
}
#news-classify h2 {
    display: inline-block;
    line-height: 30px;
}
.news-select {
    width: 200px;
    height: 30px;
    border: 1px solid #b62;
    cursor: pointer;
    outline: none;
}
/*3. 新闻标题*/
.news-title {
    margin: 30px 30px 10px;
    font: 18px/2 "Hiragino GB";
    text-align: center;
    border-bottom: 2px dotted #e8a317;
}
.loader {
    display: block;
    max-width: 100px;
    margin: 0 auto;
}
* {
    margin: 0;
    padding: 0;
}
body {
    font: 12px/1.5 Tahoma, Helvetica, Arial, "Microsoft YaHei", "Hiragino GB", "Sans-serif";
}
ul,ol{
    list-style-type:none;
}
a{
    text-decoration: none;
}
a:link{
    color:#000;
}
a:hover{
    color:#0000A0;
}
a:visited{
    text-decoration: underline;
    color:#00F;
}
a img, 
div img{
    vertical-align: top;
}






































/*新闻列表内容*/
#news-container[data-v-1ee6a627] {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
/* 新闻卡片盒子 */
.news-card[data-v-1ee6a627] {
	-webkit-box-flex: 1;
	    -ms-flex: 1 1 auto;
	        flex: 1 1 auto;
    min-width: 30%;     /*屏幕最大时一行最多排列三个卡片*/
    max-width: 400px;  /* 自适应的宽度太长时，限制一个最大宽度 */
	margin: 0 20px 20px;
	border: 1px solid #d1d0ce;
}
.card-title[data-v-1ee6a627] {
    position: relative;
    padding: 5px 20px;
    min-height: 40px;
    text-align: center;
    font-size: 13px;
    font-family: "Hiragino GB";
    background: #e7e8e1;
    -webkit-box-shadow: 2px 2px 3px 0px #827839;
            box-shadow: 2px 2px 3px 0px #827839;
    color: #2b3856;
}
.card-content[data-v-1ee6a627] {
	padding: 10px 20px 15px;
}
/* 图片不能超过原始尺寸,最大100% */
.card-content img[data-v-1ee6a627] {
	display: block;
	max-width: 100%;
	margin: 0 auto;
}
.annext[data-v-1ee6a627] {
	padding: 10px 0 30px;
}
.author[data-v-1ee6a627] {
    float: left;
}
.time[data-v-1ee6a627] {
	float: right;
}	
